$body-bg-color: #0e0e0f;

body {
  margin: 0;
  padding: 0;

  background: $body-bg-color;

  color: white; }

.svg {
  display: inline-block;

  width: 25px;
  height: 25px;

  color: inherit;

  vertical-align: middle; }

img {
  max-width: 100%; }

.content {
  color: white;
  text-align: center;

  img {
    width: 250px;

    background: white;
    border-radius: 50%; }

  h1 {
    text-transform: uppercase; }

  a {
    align-items: center;
    display: inline-flex;
    justify-content: center;

    svg {
      margin-right: 10px; } } }

.section--info {
  border-top: 1px solid #202021;
  box-shadow: inset 0 0 5px 0 #313132;

  color: #bebebe;

  h3 {
    color: white; } }

footer {
  right: 0;
  bottom: 0;
  position: fixed;

  padding: 10px;

  font-size: 12px;

  a {
    color: #0c6680;
    text-decoration: none; } }


.modal.fade .modal-dialog.modal-dialog-zoom {
  -webkit-transform: translate(0,0)scale(.5);
  transform: translate(0,0)scale(.5); }

.modal.show .modal-dialog.modal-dialog-zoom {
  -webkit-transform: translate(0,0)scale(1);
  transform: translate(0,0)scale(1); }


.checkbox--virtual {
  display: flex;
  align-items: center;

  cursor: pointer;

  input {
    display: none; }

  .icon {
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 25px;
    height: 25px;

    padding: 5px;
    margin-right: 5px;

    border-radius: 30px;
    background: #303030;
    box-shadow: 1px 1px 2px 0 #070707;

    &::before {
      content: '';

      top: 0;
      left: 0;
      position: absolute;

      width: 100%;
      height: 100%;

      border-radius: 30px;
      box-shadow: -1px -1px 2px 0 #696969; }

    svg {
      transform: scale(.8);

      color: #979797;

      transition: color .25s ease-in-out, transform .25s ease-in-out; } }

  input:checked + .icon > svg {
    transform: scale(1);

    color: #217c12; } }

