$jackpot-bg-color: #2b2b2b;
$jackpot-border-color: #737373;

.section--jackpots {
  margin: 10px 0;
  padding: 10px 0 0;

  border: 2px solid black;
  border-bottom: 0;

  .jackpot {
    min-width: 250px;
    width: max-content;

    margin: auto;

    border-radius: 30px;
    border: 3px solid $jackpot-border-color;
    background-color: $jackpot-bg-color;

    font-size: 26px;
    text-align: center;
    letter-spacing: 2px;
    font-family: fantasy;
    color: map-get($COLORS, 'gold');

    box-shadow: 0 0 5px 0 $jackpot-border-color; } }
