.bonus {
  position: relative;

  width: 170px;
  height: 115px;

  margin-right: 30px;

  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(/assets/img/bonus.png);

  &__price {
    left: 60%;
    position: relative;
    transform: translateX(-50%);

    align-self: flex-end;

    height: 30px;

    background-size: contain;
    background-repeat: repeat-x;
    background-image: effect('textbox-body.png');

    font-size: 20px;
    letter-spacing: 2px;
    font-family: fantasy;
    color: map-get($COLORS, "gold");

    &::after,
    &::before {
      content: '';

      top: 0;
      z-index: -1;
      position: absolute;

      width: 29px;
      height: 30px;

      background-size: contain;
      background-repeat: no-repeat; }

    &::before {
      right: calc(100% - 1px);

      background-image: effect('textbox-left.png'); }

    &::after {
      left: calc(100% - 1px);

      background-image: effect('textbox-right.png'); } } }
