$menu-bg-color: #2b2b2b;
$menu-item-border-color: #404040;
$menu-item-shadow-color: #191919;


.userpanel {
  z-index: 1;
  position: relative;

  padding-top: 15px;
  margin-left: 25px;

  &__avatar {
    position: relative;

    width: 130px;
    height: 100px;

    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(/assets/img/user-panel.png);

    img {
      width: 80px;
      height: 80px;

      margin-top: 11px;
      margin-left: 11px;

      object-fit: cover;
      object-position: center;

      border-radius: 80px; } }

  &__notifcation {
    top: 15px;
    right: 0;
    position: absolute;

    padding: 1px 10px;

    border-radius: 50%;
    background-color: map-get($COLORS, "danger");

    color: white;
    font-size: 20px;
    font-weight: bold; }

  &__balance {
    position: relative;

    align-self: flex-end;

    height: 42px;

    margin-left: -2px;
    margin-bottom: 3px;
    padding-left: 20px;

    background-size: contain;
    background-repeat: repeat-x;
    background-image: effect('pricebox-body.png');

    font-size: 25px;
    line-height: 42px;
    text-align: center;
    letter-spacing: 2px;
    font-family: fantasy;
    color: map-get($COLORS, "gold");

    &::after {
      content: "";

      top: 0;
      left: 100%;
      position: absolute;

      width: 22px;
      height: 42px;

      background-size: contain;
      background-repeat: no-repeat;
      background-image: effect('pricebox-right.png'); } }

  .dropdown-toggle {

    &::after {
      display: none; } }

  .dropdown-menu {
    z-index: -1;
    top: 55px !important;
    transform: translateX(-25%) !important;

    min-width: 250px;

    padding-top: 55px;
    padding-bottom: 0;

    border-radius: 30px;
    border: 4px solid map-get($COLORS, "orange");

    background-color: $menu-bg-color;

    overflow: hidden;

    .dropdown-item {
      padding-top: 5px;
      padding-bottom: 5px;

      outline: none !important;
      border: 1px solid $menu-item-border-color;
      box-shadow: 1px 0 3px 0 $menu-item-shadow-color;

      color: white;
      font-size: 20px;

      transition: background-color .25s;

      &:hover {
        background-color: darken($menu-bg-color, 2); }

      &:focus {
        background-color: darken($menu-bg-color, 3); }

      &.active,
      &:active {
        background-color: darken($menu-bg-color, 5); }

      .item__icon {
        position: relative;

        .item__counter {
          @extend .userpanel__notifcation;

          top: -5px;
          right: 20px;

          padding: 0px 6px;

          font-size: 12px; } }

      svg {
        width: 40px;
        height: 40px;

        color: #7a7a7a; }

      &:not(:last-child) {
        margin-bottom: 15px;

        svg {
          margin-right: 25px; } }

      &:last-child {
        width: max-content;

        margin: auto;
        padding-top: 10px;
        padding-bottom: 20px;

        border-bottom: 0;
        border-radius: 5px 5px 0 0; } } } }
