.section--menu {
  background-color: #171717;
  border: 2px solid #464646;
  box-shadow: 0 -2px 5px 0 #2b2b2b;

  .navbar {

    .navbar-toggler {
      color: #464646;
      border-color: #464646;

      margin: 0 10px 0 auto;

      &:focus {
        box-shadow: none; } }

    .navbar-collapse {

      .navbar-nav {
        flex: 1;

        .nav-item {

          .nav-link {
            position: relative;

            opacity: .7;

            color: white;
            font-size: 18px;
            text-align: center;

            transition: opacity .5s;

            &::after {
              content: '';

              bottom: 0;
              left: 50%;
              position: absolute;
              transform: translateX(-50%);

              width: 250px;
              height: 45px;

              background-size: contain;
              background-repeat: no-repeat;
              background-position: center bottom;
              background-image: effect('shadow.png'); }

            &:hover {
              opacity: 1; } } } } }

    .search {
      position: relative;

      margin-top: 25px;
      margin-left: auto;
      padding-left: 15px;
      padding-right: 15px;

      @media screen and (min-width: $breakpoint--lg) {
        margin-top: 0;
        padding-left: 0;
        padding-right: 15px; }

      &__input {
        overflow: hidden;

        width: 100%;
        max-width: 100%;

        transition: max-width .5s;

        @media screen and (min-width: $breakpoint--lg) {
          max-width: 320px; }

        input {
          border-radius: 30px;
          border-color: #3c3c3c;
          background-color: #171717;

          color: white;

          &:focus {
            box-shadow: none; } } }

      input[type="checkbox"]:not(:checked) + .search__input {
        @media screen and (min-width: $breakpoint--lg) {
          max-width: 38px; } }

      &__button {
        top: 0;
        right: 15px;
        position: absolute;

        display: flex;
        align-items: center;
        justify-content: center;

        width: 38px;
        height: 38px;

        outline: none;
        border-radius: 30px;
        border: 1px solid #3c3c3c;
        background-color: #171717;

        color: #3c3c3c; } } } }
