.game {
  position: relative;

  display: block;

  max-width: 200px;

  padding-bottom: 20px;

  text-decoration: none;

  &::after {
    content: '';

    bottom: 0;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);

    width: 100%;
    height: 34px;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image: effect('shadow.png');

    opacity: .5;

    transition: opacity .5s; }

  &__thumbnail {
    transition: transform .5s; }

  &:hover {

    &::after {
      opacity: 1; }

    .game__thumbnail {
      transform: scale(1.1); } }


  &__info {
    text-align: center;
    color: map-get($COLORS, "orange");

    h3 {
      font-weight: normal; } } }
