$modal-bg: #1f1e1e;
$hr-color: #313131;
$modal-color: #808080;
$form-control-bg: #c8c8c8;

.modal {
  color: $modal-color;

  .modal-content {
    background-color: $modal-bg;

    box-shadow: 0 0 5px 0 map-get($COLORS, "gold");

    .modal-title {
      font-size: 30px;
      color: map-get($COLORS, "orange"); }

    .modal-header {
      border-width: 2px;
      border-bottom-color: $hr-color; }

    .modal-footer {
      padding-top: 0;
      border-width: 2px;
      border-top-color: $hr-color;

      .btn {
        margin-top: -2px; } }

    .form-control {
      height: 55px;

      outline: none;
      border-radius: 15px;
      border: 1px solid #0f0f0f;
      background-color: $form-control-bg;
      box-shadow: 1px 1px 3px 0 #cccccca4;

      font-size: 18px;

      transition: background .5s, box-shdaow .5s ease-in-out;

      &:focus {
        background-color: white;
        box-shadow: 1px 1px 3px 0 #ccccccda; } } } }

#signInModal {

  .modal-dialog {
    max-width: 320px; } }
