.btn {
  position: relative;

  padding: 30px 10px 20px;

  outline: none;
  border: 1px solid;
  border-top: 0;
  border-radius: 0 0 10px 10px;
  box-shadow: -1px 1px 4px 0px #4c4c4c;

  background-repeat: repeat-x;
  background-position-y: -3px;

  vertical-align: top;

  cursor: pointer;

  transition: border-color .1s, box-shadow .3s ease-in-out;

  @each $name, $color in $COLORS {

    &-#{$name} {
      $is-color-lightn: lightness($color) > 40;

      border-color: darken($color, 20%);

      background-color: $color;
      background-image: effect('button-shadow-#{$name}.jpg');

      @if (lightness($color) > 40) {
        color: white; }
      @else {
        color: black; }

      &:focus,
      &:hover {
        background-color: $color;
        border-color: darken($color, 25%);

        box-shadow: -1px 1px 4px 0px #333333; }

      &:active {
        background-color: $color;

        box-shadow: inset 0px 0px 4px 0px #4c4c4c !important; }

      &.btn-color--inherit {
        .btn__text {
          color: $color; } } } } }
