.amazoncard {
  position: relative;

  width: 152px;
  height: 110px;

  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(/assets/img/amazon-card.png);

  &__price {
    left: 50%;
    bottom: 20px;
    position: absolute;
    transform: translateX(-50%) rotate(-6deg);

    margin-left: 15px;

    font-size: 16px;
    text-align: center;
    font-family: fantasy;
    color: map-get($COLORS, "gold"); } }
